/* TextBoxMenu.css */

.text-box-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  background-color: #f8f8f8;
}

.kategoria {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.menu-item {
  width: 300px;
  margin: 20px;
  padding: 15px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;

  &:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  }

  h1 {
    font-size: 1.5rem;
    margin-bottom: 10px;
  }

  img {
    width: 100%;
    border-radius: 8px;
    margin-bottom: 10px;
  }

  h2 {
    font-size: 1.2rem;
    color: #333;
    margin-bottom: 10px;
  }

  button {
    background-color: #4caf50;
    color: #fff;
    border: none;
    padding: 8px 16px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;

    &:hover {
      background-color: #45a049;
    }
  }
}

.chef-section {
  margin-top: 30px;
  text-align: center;

  h2 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 20px;
  }

  .chef-image {
    width: 300px;
    border-radius: 50%;
    margin-bottom: 20px;
  }

  p {
    font-size: 1rem;
    color: #555;
  }
}
